class Dashboard extends React.Component{
	state={
			initialloading:true,
			deg:0
		}

		componentDidMount(){
			setTimeout(()=>{
			this.setState({
					initialloading:false
				});
			},2000);

			document.addEventListener('keydown',this.handleKeyPress);
			changebg();

		}

		  handleKeyPress=()=>{
		  	switch (window.event.keyCode) {
		        case 37:
		         console.log("left"); 
		         break;
		        case 38:
		         this.handlecssup(window.event);
		         break;
		        case 39:
				 console.log("right");
		         break;
		        case 40:
				  	this.handlecssdown(window.event);
		         	break;
		        
		    }
		  }


		handlecssdown=e=>{
			e.preventDefault();
			this.setState({
				deg:this.state.deg+180
			},function(){
				
				let cube=document.getElementById("cube");

				let currButton = document.getElementById("buttonele");
				let buttondeg = this.state.deg*4;
				currButton.style.transform='translate3d(0,0,-50vh) rotateX('+buttondeg+'deg)';

				cube.style.transform='translate3d(0,0,-50vh) rotateX('+this.state.deg+'deg)';
				cube.style.transition="all 1s cubic-bezier(0.575,0.885,0.320,1.275)";

			});
		}

		handlecssup=e=>{
			e.preventDefault();
			this.setState({
				deg:this.state.deg-180
			},function(){
				let cube=document.getElementById("cube");
				cube.style.transform='translate3d(0,0,-50vh) rotateX('+this.state.deg+'deg)';
				cube.style.transition="all 1s cubic-bezier(0.175,0.885,0.320,1.275)";

			});
		}

	render(){
			const {classes,...rest}=this.props;
			const {initialloading,deg}=this.state;
			console.log(deg);
			return(
				<div className={classes.body}>
						{initialloading && <Loading/>}
						{!initialloading &&
							<div >
								
								<Cubic/>
								{/* {!((this.state.deg%360)==0)&&<div className={classes.icondivup} onClick={this.handlecssup}>
								  		<div className={s.arrowup}>
										                <span style={{borderTop:'5px solid #FFF',
										                			  borderLeft: '5px solid #FFF'}}>
										                </span>
										                <span style={{borderTop:'5px solid #06A8FF',
										                			  borderLeft: '5px solid #06A8FF'}}>
										                </span>
										                <span style={{borderTop:'5px solid #000',
										                			  borderLeft: '5px solid #000'}}>
										                </span>
										</div>
								</div>
								} */}

								{/* <div className={classes.icondivdown} onClick={this.handlecssdown}>
								  		<div className={s.arrowdown}>
										                <span style={{borderBottom:'5px solid #FFF',
										                			  borderRight: '5px solid #FFF'}}>
										                </span>
										                <span style={{borderBottom:'5px solid #06A8FF',
										                			  borderRight: '5px solid #06A8FF'}}>
										                </span>
										                <span style={{borderBottom:'5px solid #000',
										                			  borderRight: '5px solid #000'}}>
										                </span>
										  
										</div>
								</div> */}
								
							</div>
						}





						<DPStyled id="aboutbody" >
						
								<div
									style={{
										background:"white",
										height:"100%",
										backgroundRepeat:"no-repeat",
										backgroundPosition:"center",
										backgroundSize:"100% 100%",
										background: "linear-gradient(-45deg, rgba(0,0,0,0.22), rgba(255,255,255,0.25))",
  										boxShadow: "12px 12px 16px 0 rgba(0, 0, 0, 0.25),-8px -8px 12px 0 rgba(255, 255, 255, 0.3)",
										  borderRadius:"20px",
										
										}}>
								<div
									style={{
										borderRadius:"40px",
										backgroundImage: "url(" + gif + ")",
										height:"100%",
										backgroundRepeat:"no-repeat",
										backgroundPosition:"center",
										backgroundSize:"90% 90%",

									}}></div>

								</div>
								
						</DPStyled>

						<NavStyled onClick={this.handlecssdown} id="buttonele">
																
						</NavStyled>
				</div>
				);
	}
}



